<!-- + -->
<template>
  <div :style="{width: chartWidth + 'px', height: chartHeight + 'px'}">
    <IEcharts :option="barPies"></IEcharts>
  </div>
</template>

<script>
    import IEcharts from 'vue-echarts-v3/src/lite.js'
    import 'echarts/theme/dark'
    
    let dataAll = [42.65,48.8,51.69,41.69,32.9,13,40,46,48.8,50.26,53.3,33,47.5,25,32.7,77.51,45,24,58.83,61.9,18,38.46];
    let xAxisData = ['','','','','','','','','','','','','','','','','','','','','',''];
    let pieData = [{value:110.24,  name: ''},
{value:92.11,   name: ''},
{value:54.46,   name: ''},
{value:101.45,  name: ''},
{value:1381,    name: ''},
{value:69.98,   name: ''},
{value:676,     name: ''},
{value:2648,    name: ''},
{value:525,     name: ''},
{value:524,     name: ''},
{value:619,     name: ''},
{value:347,     name: ''},
{value:468,     name: ''},
{value:296.39,  name: ''},
{value:1069,    name: ''},
{value:863,     name: ''},
{value:1025,    name: ''},
{value:1210,    name: ''},
{value:675,     name: ''},
{value:951,     name: ''},
{value:222,     name: ''},
{value:603,      name: ''}]

    export default{
      components:{
        IEcharts
      },
      props:{
        chartWidth:{
          default: 940
        },
        chartHeight:{
          default: 615
        }
      },
      mounted:function () {
        
      },
      watch:{
        //items
        
      },
      methods:{        
        findChartOptionData:function(){
            //items[{name:'',value:''}] nametitle
          this.gauge.series[0].data = this.items;
        }
      },
      data:function () {
        return {            
          //
          barPies:{
                  title:[
                  {text:"",x: '10%', y: '5%',textStyle:{color:"#fff",fontSize:"14"}},
                  {text:"",x: '45%', y: '5%',textStyle:{color:"#fff",fontSize:"14"}}
            ],
            grid: [
                {x: '50%', y: '15%', width: '50%', height: '75%'},
            ],
            tooltip: {
                formatter: '{b} [{c}]'
            },
             xAxis: [
                {                  
                  type : 'category',
                  nameRotate : 10,
                  axisTick: {alignWithLabel: true},
                  data:xAxisData.reverse(),
                  axisLine: {show:true,lineStyle:{color:"#6173a3"}},              
                  axisTick: {show:false},
                  axisLabel: {rotate : 30,show:true},
                  splitLine: {show:false}
                }
            ],                            
            yAxis: [
                    {
                        type: 'value',
                        name: '',
                        position: 'left',
                        axisLine: {
                            lineStyle: {
                                color: '#fff'
                            }
                        },
                        splitLine: {show:false},
                        axisLabel: {
                          formatter: '{value} ',
                          // show:true,
                          textStyle:{
                            color:'#fff'
                          }
                        }
                    }
            ],
            series: [
                {
                    name: '',
                    type: 'pie',
                    radius : '30%',
                    center: ['20%', '50%'],
                    color:['#86c9f4','#4da8ec','#3a91d2','#005fa6','#315f97'],
                    data:pieData,
                    labelLine:{normal:{show:false}},
                    itemStyle: {normal: {label:{ rotate:30,show: true,  formatter: '{b} \n ({d}%)', textStyle:{color:'#B1B9D3'}} },},
                },
                {
                    name: 'top5',
                    type: 'bar',xAxisIndex: 0,yAxisIndex: 0,barWidth:'25%',
                    itemStyle:{normal:{color:['#86c9f4','#4da8ec','#3a91d2','#005fa6','#315f97']}},
                    label:{normal:{show:false, position:"right",textStyle:{color:"#9EA7C4"}}},
                    data: dataAll
                }                
            ]
        }
      }
    }
  }
</script>



