<!--  -->
<template>
  <div :style="{width: chartWidth + 'px', height: chartHeight + 'px'}">
    <IEcharts :option="radars"></IEcharts>
  </div>
</template>

<script>
    import IEcharts from 'vue-echarts-v3/src/lite.js'
    import 'echarts/lib/chart/radar'
    import 'echarts/theme/dark'
    export default{
      components:{
        IEcharts
      }, 
      props:{
        chartWidth:{
          default: 440
        },
        chartHeight:{
          default: 400
        }
      },     
      mounted:function () {
        //
        
      },     
      methods:{
        
      },
      data:function () {
        return {
          radars:{
            // title: {
            //     text: ''
            // },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                x: 'center',
                data:['','','',''],
                textStyle:{color:"#fff"}
            },
            radar: [
                
                {
                    indicator: [
                        {text: '', max: 3000},
                        {text: '', max: 3000},
                        {text: '', max: 3000},
                        {text: '', max: 100},
                        {text: '', max: 100}
                    ],
                    radius: 135,
                    shape: 'circle',
                    center: ['50%','50%'],
                }
            ],
            series: [                
                {
                    type: 'radar',
                    radarIndex: 0,
                    data: [
                        {
                            value: [2565, 2434, 2274, 72, 98],
                            name: ''
                        },
                        {
                            value: [1775, 1762, 1660, 80, 98],
                            name: ''
                        },
                        {
                            value: [1314, 1300, 1255, 77, 97],
                            name: ''
                        },
                        {
                            value: [2123, 2097, 2017, 82, 98],
                            name: ''
                        }
                    ]
                }
              ]
            }
        }
      }
    }
</script>



