<!-- + -->
<template>
  <div :style="{width: chartWidth + 'px', height: chartHeight + 'px'}">
    <IEcharts :option="barRadar"></IEcharts>
  </div>
</template>

<script>
    import IEcharts from 'vue-echarts-v3/src/lite.js'
    import 'echarts/lib/chart/radar'
    import 'echarts/theme/dark'
    //---------------------------------------------------------------------------
    //x
    let xAxisBarChart = []
    //
    let dataBarChart = []
    //7
    let dataBrownies = []
    //7
    let dataMacarons = []
    //7
    let dataTiramisu = []
    //
    let timelineData = []
    //
    let optionsData = []
    //------------------------------------------------------------------------------------------------------------
    //
    let weekDay = 0
    //
    let sourceArray = []
    //
    let SortOrNot = false
    //
    let compare = function(x, y) {
            if (x.value < y.value) {
                return 1
            } else if (x.value > y.value) {
                return -1
            } else {
                return 0
            }
        }
    //
    let dataBarChartMap = new Map()
    //dataBarChart
    let dataBarChartTemp = []

    //map,
        function Map() {
            this.elements = new Array();

            // Map
            this.size = function() {
                    return this.elements.length;
                },

                // Map
                this.isEmpty = function() {
                    return (this.elements.length < 1);
                },

                // Map
                this.clear = function() {
                    this.elements = new Array();
                },

                // Mapkey, value)
                this.put = function(_key, _value) {
                    if (this.containsKey(_key) == true) {
                        if (this.containsValue(_value)) {
                            if (this.remove(_key) == true) {
                                this.elements.push({
                                    key: _key,
                                    value: _value
                                });
                            }
                        } else {
                            this.elements.push({
                                key: _key,
                                value: _value
                            });
                        }
                    } else {
                        this.elements.push({
                            key: _key,
                            value: _value
                        });
                    }
                },

                // keytruefalse
                this.remove = function(_key) {
                    var bln = false;
                    try {
                        for (i = 0; i < this.elements.length; i++) {
                            if (this.elements[i].key == _key) {
                                this.elements.splice(i, 1);
                                return true;
                            }
                        }
                    } catch (e) {
                        bln = false;
                    }
                    return bln;
                },

                // keyvaluenull
                this.get = function(_key) {
                    try {
                        for (i = 0; i < this.elements.length; i++) {
                            if (this.elements[i].key == _key) {
                                return this.elements[i].value;
                            }
                        }
                    } catch (e) {
                        return null;
                    }
                },

                // element.keyelement.valuekeyvaluenull
                this.element = function(_index) {
                    if (_index < 0 || _index >= this.elements.length) {
                        return null;
                    }
                    return this.elements[_index];
                },

                // Mapkey
                this.containsKey = function(_key) {
                    var bln = false;
                    try {
                        for (i = 0; i < this.elements.length; i++) {
                            if (this.elements[i].key == _key) {
                                bln = true;
                            }
                        }
                    } catch (e) {
                        bln = false;
                    }
                    return bln;
                },

                // Mapvalue
                this.containsValue = function(_value) {
                    var bln = false;
                    try {
                        for (i = 0; i < this.elements.length; i++) {
                            if (this.elements[i].value == _value) {
                                bln = true;
                            }
                        }
                    } catch (e) {
                        bln = false;
                    }
                    return bln;
                },

                // Mapkeyarray
                this.keys = function() {
                    var arr = new Array();
                    for (i = 0; i < this.elements.length; i++) {
                        arr.push(this.elements[i].key);
                    }
                    return arr;
                },

                // Mapvaluearray
                this.values = function() {
                    var arr = new Array();
                    for (i = 0; i < this.elements.length; i++) {
                        arr.push(this.elements[i].value);
                    }
                    return arr;
                };
        }

    export default{
      components:{
        IEcharts
      },
      props:{
        chartWidth:{
          default: 100
        },
        chartHeight:{
          default: 100
        },
        items:{
          default:[]
        },
      },
      mounted:function () {
        //
        for (var week = 0; week < 26; week++) {
            var weekDataArray = []
            for (var day = 0; day < 7; day++) {
                 var productSales = [{
                        'Brownies': parseInt(20 * Math.random()),
                        'Macarons': parseInt(20 * Math.random()),
                        'Tiramisu': parseInt(20 * Math.random())
                    }];
                    weekDataArray.push(productSales);
            }
            //3726
            sourceArray.push(weekDataArray)
            timelineData.push('' + (week + 1) + '')
        }
        this.everyProductCalculate()
      },
      watch:{
        //items
        items : {
          handler : function(val,oldVal){
            this.findChartOptionData();
          },
          deep:true
        }
      },
      methods:{        
        //
        everyProductCalculate:function(){
          //
          this.dataBarChartMap = new Map()
          dataBarChartTemp = []
          xAxisBarChart = []
          dataBrownies = []
          dataMacarons = []
          dataTiramisu = []
          optionsData = []

          //
          for (var i = 0; i < sourceArray.length; i++) {
              var weekSum = 0
              for (var j = 0; j < sourceArray[i].length; j++) {
                  var daySum = 0
                  daySum = sourceArray[i][j][0].Brownies +
                      sourceArray[i][j][0].Macarons +
                      sourceArray[i][j][0].Tiramisu
                  weekSum = weekSum + daySum
              }
              dataBarChartTemp.push(weekSum)
          }
          //map
          for (var i = 1; i < sourceArray.length + 1; i++) {
              this.dataBarChartMap.put(i, dataBarChartTemp[i - 1])
          }
          //
          if (SortOrNot) {
              this.dataBarChartMap.elements.sort(compare)
              dataBarChartTemp = this.dataBarChartMap.elements
          }
          //dataBarChart
          dataBarChart = dataBarChartTemp
          for (var i = 0; i < sourceArray.length; i++) {
              var xAxisBarChartTemp = []
              for (var j = 0; j < sourceArray.length; j++) {
                  if (i == this.dataBarChartMap.elements[j].key - 1) {
                      xAxisBarChartTemp.push('\n>' + this.dataBarChartMap.elements[j].key + '<')
                  } else {
                      xAxisBarChartTemp.push(this.dataBarChartMap.elements[j].key + '')
                  }
              }
              //xAxisBarChartx
              xAxisBarChart.push(xAxisBarChartTemp)
          }
          //
          //7
          for (var i = 0; i < sourceArray.length; i++) {
              this.dataBrowniesTemp = []
              this.dataMacaronsTemp = []
              this.dataTiramisuTemp = []
              for (var j = 0; j < sourceArray[i].length; j++) {
                  this.dataBrowniesTemp.push(sourceArray[i][j][0].Brownies)
                  this.dataMacaronsTemp.push(sourceArray[i][j][0].Macarons)
                  this.dataTiramisuTemp.push(sourceArray[i][j][0].Tiramisu)
              }
              //
              dataBrownies.push(this.dataBrowniesTemp)
              dataMacarons.push(this.dataMacaronsTemp)
              dataTiramisu.push(this.dataTiramisuTemp)
          }
          for (var i = 0; i < sourceArray.length; i++) {
              optionsData.push({
                  'series': [{
                          'data': dataBrownies[i]
                      }, {
                          'data': dataMacarons[i]
                      }, {
                          'data': dataTiramisu[i]
                      }, {
                          'data': [{
                              value: dataBrownies[i][weekDay],
                              name: ''
                          }, {
                              value: dataMacarons[i][weekDay],
                              name: ''
                          }, {
                              value: dataTiramisu[i][weekDay],
                              name: ''
                          }]
                      }, {
                          'data': dataBarChart
                      },

                  ],
                  'xAxis': [{
                      'data': xAxisBarChart[i],
                  }]
              })
          }
          this.barRadar.options = optionsData
        },
        //
        tooltipFormatter:function(params) {
            var valuesFormatter = [];
            if (params.componentSubType == 'pie') {
                valuesFormatter
                    .push('<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">' +
                        '' +
                        '<br>' +
                        '</div>' +
                        '<span style="color:' +
                        params.color +
                        '">' + params.name + '</span>: ' + params.value);
            } else {
                valuesFormatter
                    .push('<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">' +
                        params.seriesName +
                        '</div>' +
                        '<span style="color:' +
                        params.color +
                        '">' +
                        params.name +
                        '</span>: ' +
                        params.value + '<br>');
            }

            return valuesFormatter;
        },
        findChartOptionData:function(){
            //items[{name:'',value:''}] nametitle
          this.gauge.series[0].data = this.items;
        }
      },
      data:function () {
        return {            
          //
          barRadar:{
            baseOption: {
              timeline: {
                  x: 0,
                  axisType: 'category',
                  orient: 'vertical',
                  autoPlay: true,
                  playInterval: 1000,
                  data: timelineData,
                  left: 10,
                  right: null,
                  top: 50,
                  bottom: 0,
                  width: 70,
                  height: null,
                  inverse: true,
                  symbol: 'none',
                  lineStyle: {
                      color: '#555'
                  },
                  checkpointStyle: {
                      color: '#bbb',
                      borderColor: '#777',
                      borderWidth: 2
                  },
                  label: {
                      normal: {
                          textStyle: {
                              color: '#999',
                              fontSize: 15
                          }
                      },
                      emphasis: {
                          textStyle: {
                              color: '#999',
                              fontSize: 20
                          }
                      }
                  }
              },
              title: {
                  text: 'Snow-',
                  subtext: '\n',
                  textAlign: 'left'
              },
              tooltip: {
                  trigger: 'item',
                  padding: 10,
                  backgroundColor: '#222',
                  borderColor: '#777',
                  borderWidth: 1,
                  formatter: this.tooltipFormatter
              },
              angleAxis: {
                  type: 'category',
                  data: [{
                      value: '',
                      textStyle: {
                          fontSize: 25,
                      }
                  }, '', '', '', '', '', ''],
                  z: 10
              },
              grid: {
                  left: '6%',
                  right: '5%',
                  top: '16%',
                  bottom: '3%',
                  containLabel: true
              },
              polar: {
                  center: ['80%', '30%'],
                  radius: 100,
              },
              xAxis: [{
                  type: 'category',
                  data: xAxisBarChart,
                  axisLabel: {
                      'interval': 0
                  },
              }],
              yAxis: [{
                  type: 'value',
                  max: 600,
                  show: false,
              }],
              radiusAxis: {},
              series: [{
                  type: 'bar',
                  data: dataBrownies,
                  coordinateSystem: 'polar',
                  name: '',
                  center: ['80%', '30%'],
                  stack: 'a',
                  itemStyle: {
                      normal: {
                          borderWidth: 4,
                          borderColor: '#ffffff',
                      },
                      emphasis: {
                          borderWidth: 0,
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }, {
                  type: 'bar',
                  data: dataMacarons,
                  coordinateSystem: 'polar',
                  name: '',
                  center: ['80%', '30%'],
                  stack: 'a',
                  itemStyle: {
                      normal: {
                          borderWidth: 4,
                          borderColor: '#ffffff',
                      },
                      emphasis: {
                          borderWidth: 0,
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }, {
                  type: 'bar',
                  data: dataTiramisu,
                  coordinateSystem: 'polar',
                  name: '',
                  center: ['80%', '30%'],
                  stack: 'a',
                  itemStyle: {
                      normal: {
                          borderWidth: 3,
                          borderColor: '#ffffff',
                      },
                      emphasis: {
                          borderWidth: 3,
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }, {
                  name: '',
                  type: 'pie',
                  center: ['80%', '30%'],
                  radius: ['45%', '50%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: true,
                          position: 'outside',
                          formatter: '{b} : {c} ({d}%)'

                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '15',
                              fontWeight: 'normal'
                          }
                      },
                      tooltip: {
                          trigger: 'item',
                          padding: 10,
                          backgroundColor: '#222',
                          borderColor: '#777',
                          borderWidth: 1,
                          formatter: this.tooltipFormatter
                      }
                  },
                  labelLine: {
                      normal: {
                          show: false
                      }
                  },
                  legend: {
                      show: true,
                      orient: 'vertical',
                      x: 'left',
                      y: 'bottom',
                      data: ['', '', '']
                  },
                  itemStyle: {
                      normal: {
                          borderWidth: 3,
                          borderColor: '#ffffff',
                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '15',
                              fontWeight: 'normal'
                          }
                      }
                  }
              }, {
                  name: '',
                  type: 'bar',
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: true,
                          position: 'outside',
                          formatter: '{c}'

                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '20',
                              fontWeight: 'normal'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: false
                      }
                  },
                  itemStyle: {
                      normal: {
                          borderWidth: 3,
                          borderColor: '#ffffff',
                      },
                      emphasis: {
                          borderWidth: 3,
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }]
            },
            options: optionsData              
        }
      }
    }
  }
</script>



